import React, {useState, useEffect} from "react";
import {useHistory, useParams} from "react-router-dom";
import styled from 'styled-components';
import './Detail.scss';

let 박스 = styled.div`
    padding : 20px;
`;

let 제목 = styled.h4`
    font-size: 25px;
    color : ${ props => props.색상}
`;

function Detail(props) {

    let [alert, alert변경] = useState(true);
    let [input, input변경] = useState('');

    useEffect( ()=> {
       let Timer = setTimeout( ()=>{alert변경(false)}, 2000);
       console.log('안녕');
        // return 사용시 => <Detail> 컴포넌트가 사라질때 특정 코드 실행(Timer해제)
        // 콜백함수 사용가능
       return ()=>{ clearTimeout(Timer) }
    },[]); // ,[조건] => 실행조건   빈칸이면 => <Detail> 등장시 한번 실행하고 끝남

    // {사용자가 입력한 URL 파라미터 값이 넘어옴}
    let { id } = useParams();
    //return 오른쪽에 조건식을 만족하면, '찾은상품'에 값 이 저장됨
    let 찾은상품 = props.shoes.find(function (상품) {
        return 상품.id == id
    });
    let history = useHistory();

    return (
        <div className="container">
            <박스>
                <제목 className="red"> Detail </제목>
            </박스>

            {/*e.target.value 현재 입력된 값*/}
            <input onChange={ (e)=>{input변경(e.target.value)} } />
            {
                alert === true
                ? (Alert())
                : null
            }
            <div className="row">
                <div className="col-md-6">
                    <img src="https://codingapple1.github.io/shop/shoes1.jpg" width="100%" />
                </div>
                <div className="col-md-6 mt-4">
                    <h4 className="pt-5">{찾은상품.title}</h4>
                    <p>{찾은상품.content}</p>
                    <p>{찾은상품.price}원</p>

                    <Info 재고={props.재고}></Info>

                    <button className="btn btn-danger" onClick={ ()=>{ props.재고변경([...props.재고 - 1 ]) } }>주문하기</button>
                    &nbsp;
                    <button className="btn btn-danger" onClick={ ()=>{ history.push('/') } }>뒤로가기</button>
                </div>
            </div>
        </div>
    )
}


function Alert() {
    return(
        <div className="my-alert-yellow">
            <p>재고가 얼마 남지 않았습니다.</p>
        </div>
    )
}

function Info(props) {
    return(
        <p>재고 : {props.재고[0]}</p>
    )
}
export default Detail;